@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
/* Import Google fonts */

* {
  padding: 0;
  margin: 0;
}

ol,
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
  margin: 0;
  padding: 0;
}

.links,
a {
  text-decoration: none;
}

button {
  outline: none;
}

fieldset {
  border: 0;
}

input {
  font-size: 16px;
  outline: none;
}

img {
  max-width: 100%;
}

