html {
  min-width: auto;
}

body {
  margin: 0;
  color: rgb(131, 124, 124);
  color: var(--font-color);
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
  font-family: var(--font-family);
  font-size: 13px;
  background: #fff;
  background: var(--white);
  max-width: 100%;
  padding-top: 50px;
  min-height: 100%;
  background-image: none;
}

input:active,
input:focus,
button:active,
button:focus {
  outline: none;
}

.custom-btn-light-blue {
  color: #0077cc;
  color: var(--blue-600);
  background: #cfeafe;
  background: var(--blue-100);
  border: 1px solid hsl(206, 100%, 52%);
  border: 1px solid var(--blue-500);
  border-radius: 3px;
  text-align: center;
  cursor: pointer;
}

.custom-btn-light-blue:hover {
  background: #addafc;
  background: var(--blue-200);
}

.custom-btn-dark-blue {
  background: hsl(206, 100%, 52%);
  background: var(--blue-500);
  border-radius: 3px;
  text-align: center;
  border: 1px solid #0077cc;
  border: 1px solid var(--blue-600);
  min-width: 3rem;
}

.custom-btn-dark-blue:hover {
  background: #0064bd;
  background: var(--blue-700);
}
.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 0;
  position: relative;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.content{
    position: relative;
    height: 100%;
    width: 100%;
    overflow-x: hidden;
    display: grid;
    grid-template-columns: 30vh 2fr 1fr;
    z-index: 1;
}

.aside-left{
    width: 30vh;
    padding-top: 5rem;
    border-right: 2px solid var(--black-050);
}

.questions-wrapper {
    width: 100%;
}

.aside-right {
    box-sizing: border-box;
    position: relative;
    z-index: 3;
}

.footerSection{
    background:teal;
    color:white;
    height: 10rem;
    width: 10rem;
    position: relative;
    margin: 20px ;

}

.main-top-grid {
    display: flex;
    flex: 0 0 auto;
    padding: 20px 5px 10px 10px;
}

.main-top-grid h1 {
    flex: 0 0 auto;
    font-size: 2.07692308rem !important;
}

.ask-question-btn {
    flex: 1 0 auto;
    text-align: right;
    padding: 15px 0;
}

.ask-question-btn a {
    color: #fff;
    background-color: var(--blue-500);
    border-radius: 3px;
    text-align: center;
    cursor: pointer;
    padding: 10px;
}

.data-query {
    flex: 0 0 auto;
    display: flex;
    justify-content: space-between;
    padding: 10px 5px 10px 5px;
}

.in-archaive {
    flex: 1 0 120px;
    padding: 10px 5px;
    text-align: left;
}

.query {
    flex: 0 0 auto;
    display: flex;
    justify-content: flex-end;
    border-radius: 2px;
    border: 1px solid var(--black-300);
}

.newest,
.active,
.unanswered {
    color: var(--black-300);
    padding: 6px;
}

.active {
    background: var(--black-200);
    color: var(--black-600);
    border: 1px solid var(--black-300);
    border-top-width: 0;
    border-bottom-width: 0;
    border-left-width: 1px;
    border-right-width: 1px;
}



/** Media query for tablets */
@media screen and (max-width: 1024px) {
    .left-sidebar {
        display: none;
    }
}

/** Media query for smartphones**/
@media screen and (max-width: 480px) {
    .left-sidebar {
        display: none;
    }

    .main {
        flex: 0 0 100%;
    }

    .main-right {
        display: none;
    }

    .main-top-grid {
        padding: 20px px;
    }

    .main-top-grid h1 {
        padding-top: 10px;
        font-size: 1.07692308rem !important;
    }

    .query {
        justify-content: space-evenly;
    }
}
.nav-icon-mobile,
.smtphone-search-icon,
.sideNav {
    display: none;
}

.header-wrapper {
    position: relative;
    z-index: 5;
}

.notify {
    position: fixed;
    left: 0;
    top: 0;
    height: 3px;
    width: 100%;
    z-index: 5051;
    background: var(--orange);
}

.brand-logo-wrapper {
    flex: 13em 0;
    min-width: 13em;
}

/* Main Nav */
.nav {
    background: var(--grey-light);
    position: fixed;
    top: 3px;
    left: 0;
    text-align: center;
    padding: 0;
    width: 100%;
    border: 1px solid var(--shadow);
    box-shadow: var(--shadow);
}

.nav-box {
    display: flex;
    height: 45px;
    margin: 0 3%;
    grid-gap: 5px;
    gap: 5px;
}

.nav-link {
    align-self: center;
    border-radius: 15px;
    font-size: 0.9rem;
    padding: 8px 0;
    cursor: pointer;
    min-width: 5.5rem;
}

.nav-link:hover {
    background: var(--ash);
}

.nav-link:active,
.nav-link:focus {
    background: var(--orange);
    color: var(--white);
}

.auth-btns {
    align-self: center;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 10;
}

.login-btn,
.signup-btn {
    padding: .6em .9em;
    margin: 0 5px;
}

.signup-btn .links {
    color: var(--white);
}

/** Media query for tablets */
@media screen and (max-width: 1024px) {

    .brandlogo {
        font-size: 15px;
    }

    .menulinks .bar-item {
        padding: 0 10px 0 10px;
    }

    .smtphone-search-icon {
        display: none;
    }

}

/** Media query for smartphones**/
@media screen and (max-width: 480px) {
    .menulinks .bar-item {
        padding: 0 5px 0 5px;
    }

    .menubtns {
        margin: 0 5px 0 10px;
    }

    .menubtns .bar-item {
        padding: 4px 4px 4px 4px;
    }

    .menu-icon-mobile {
        display: block;
        font-size: 2rem;
    }

    .sideNav {
        display: block;
        width: 70%;
        position: fixed;
        top: 3.5rem;
        transition: 840ms;
    }

    .sideNav ul {
        float: left;
        margin: 5px 0 0 10px;
    }

    .sideNav.hidden {
        display: none;
    }

    .brandlogo {
        display: none;
    }

    .smtphone-search-icon {
        display: inline-block;
        height: 2rem;
        font-size: 1.3rem;
        cursor: pointer;
        color: var(--black-400);
        padding: 10px 6px 0px 6px;
        margin: 0 2px 0 2px;
    }

    .smtphone-searchbar {
        background: var(--black-050);
        padding: 8px;
    }

}
.auth-form {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}

.auth-form h1 {
    font-size: 2.3rem;
    margin-top: 20px;
}

.auth-form fieldset {
    flex: 0 0 auto;
    border-radius: 50px;
    margin-top: 15px;
    background: var(--black-050);
    width: 100%;
    max-width: 450px;
    display: grid;
    grid-template-columns: 3rem auto;
    padding: 5px;
}

.auth-form fieldset input {
    background: none;
    border: none;
    line-height: 1;
    font-weight: 600;
    font-size: 1.1rem;
    color: #333;
    width: 100%;
}

.auth-form fieldset i {
    text-align: center;
    line-height: 50px;
    color: #acacac;
    transition: 0.5s;
    font-size: 1.1rem;
}

.btn, .btn {
    width: 12rem;
    height: 40px;
    font-weight: 600;
    font-size: 0.8rem;
    margin-top: 20px;
    border: 2px solid var(--blue-900);
    border-radius: 50px;
    cursor: pointer;
}

.auth-form p {
    margin-top: 20px;
    font-size: 1.1rem;
}

.auth-form .social-media {
    margin-top: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 5px;
}

.auth-form .social-media .social-icon {
    flex: 0 0 auto;
    margin: 0 0.50rem;
    color: #333;
    border-radius: 50%;
    border: 0;
    text-decoration: none;
    font-size: 1.1rem;
    transition: 0.3s;
    height: 3rem;
    width: 3rem;
    display: flex;
    justify-content: center;
    align-items: center;
}

/* Drop down login form styled */
.hideDropDownLoginForm {
    display: none;
}

.showDropDownLoginForm {
    display: block;
    position: absolute;
    top: 45px;
    right: 5px;
    z-index: 100;
    background: var(--grey-dark);
    padding: 10px;
    border-radius: 10px;
}

.showDropDownLoginForm div {
    margin: 10px;
}

.showDropDownLoginForm div>input {
    border: none;
    border-radius: 10px;
    padding: 5px 10px;
}

.showDropDownLoginForm div>button {
    color: var(--white);
    padding: 5px 20px;
}

/* Drop down login form styled end*/


/** MEDIA QUERY*/
@media screen and (max-width:1000px) {

    .auth-form h1 {
        font-size: 2rem;
        margin-top: 5px;
    }

    .auth-form fieldset {
        max-width: 250px;
    }

    .auth-form fieldset input {
        line-height: 1;
        font-weight: 600;
        font-size: 1.1rem;
    }

    .auth-form fieldset i {
        text-align: center;
        line-height: 45px;
        font-size: 1.1rem;
    }

    .auth-form .signup-btn {
        width: 10rem;
        font-weight: 500;
    }

    .auth-form p {
        font-size: 1rem;
    }

    .auth-form .social-media {
        margin: 5px;
    }

    .auth-form .social-media .social-icon {
        font-size: 1rem;
        height: 2.5rem;
        width: 2.5rem;
    }
}

@media screen and (max-width: 800px) {
    .auth-form {
        max-width: 450px;
    }

    .auth-form fieldset input {
        line-height: 1;
        font-weight: 400;
        font-size: 1rem;
    }

    .auth-form fieldset i {
        text-align: center;
        line-height: 35px;
        font-size: 1rem;
    }
}

@media screen and (max-width: 580px) {}
.searchbar-4smtphone {
    display: none;
}

.searchbar {
    padding: 0 10px 0 10px;
    border-radius: 3px;
    border: 1px solid var(--black-200);
    flex: 1 1;
    align-self: center;
    background: var(--white);
}

.searchbar .ps--relative {
    display: flex;
}

.searchbar .s-input {
    box-shadow: none;
    color: var(--black-700);
    width: 100%;
    border: 1px solid var(--white);
    border-radius: 3px;
}

.searchbar:hover,
.searchbar:focus {
    border: 2px solid var(--blue-200);
    box-shadow: 0 0 5px var(--blue-400);
}

.s_icon {
    color: var(--black-350);
    vertical-align: bottom;
    padding: .3em .2em;
    pointer-events: none;
    font-size: 1.1rem;
}

/** Media query for tablets */

@media screen and (max-width: 1024px) {
    .searchbar {
        padding-left: 5px !important;
        padding-right: 5px !important;
    }

    .searchbar .s-input {
        border: var(--white);
    }
}

/** Media query for smartphones**/

@media screen and (max-width: 480px) {
    .searchbar {
        display: none;
    }

    .searchbar-4smtphone {
        display: inline-block;
        border-radius: 5px;
        border-radius: 3px;
        border: 1px solid var(--black-200);
        width: 22rem;
    }

    .searchbar-4smtphone .ps--relative {
        display: flex;
    }

    .searchbar-4smtphone input {
        border: var(--white);
        width: 100%;
    }
}
