.hide-popover {
    display: none;
}

.popover {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: start;
    position: absolute;
    grid-gap: 15px;
    gap: 15px;
    top: 46px;
    left: 205px;
    border: 1px solid var(--ash);
    border-radius: 7px;
    width: 15rem;
    background: var(--white);

}

.popover ol {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: start;
    cursor: pointer;
    padding: 10px 5px;
    grid-gap: 15px;
    gap: 15px;
    width: 100%;
}

.popover>ol>li {
    text-align: left;
    border-radius: 3px;
    padding: 5px;
    width: 12.3rem;

}

.popover>ol>li:hover {
    background: var(--ash);
    border: 1px solid var(--ash);
    border-radius: 5px;
}

.popover>ol>li .popover-title {
    color: var(--black-750);
}

.popover>ol>li .popover-content {
    color: var(--black-350);
}

.popover>div {
    background: var(--black-350);
    border: 1px solid var(--ash);
    height: 2.5rem;
    width: 100%;
    background: var(--grey-light);
    display: flex;
    justify-content: flex-start;
    align-items: center;
    cursor: pointer;
}

.popover>div span {
    margin-left: 20px;
    color: var(--black-350);
}

.popover>div span:hover {
    color: var(--black-750);
}

.hide-popover-power {
    display: none;
}
.popover-arrow {
    height: 13px;
    width: 13px;
    position: absolute;
    top: 42px;
    -webkit-transform: translateX(300px) rotate(135deg);
            transform: translateX(300px) rotate(135deg);
    background: var(--white);
    border-bottom: 1px solid var(--ash) ;
    border-left: 1px solid var(--ash) ;
}
.question-summary {
    display: flex;
    border-top: 1px solid var(--black-100);
    /*margin-top: 10px;*/
    width: 100%;
    z-index: 5;
}

.summary {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.votes,
.answers,
.views {
    padding: 18px 10px 10px 10px;
}

.votes span {
    font-size: 2rem;
    padding: 10px;
}

.answers span {
    font-size: 1rem;
    padding: 10px;
}
.views {
    color: var(--red-500);
}

.question {
    display: flex;
    flex-direction: column;
    padding-top: 10px;
    text-align: left;
}

.title {
    color: var(--blue-600);
    text-decoration: none;
    font-size: 18px;
}

.question-body {
    padding: 10px 0 10px 0;
}

.tags li {
    display: inline;
    background: var(--blue-100);
    color: var(--blue-600);
    font-size: 10px;
    margin: 0 5px 0 5px;
    padding: 3px 6px 3px 6px;
    border-radius: 3px;
}

@media screen and (max-width: 480px) {
    .question-summary {
        display: flex;
        height: 11rem;
        margin-left: 5px;
    }

    .summary {
        width: 9rem;
        margin: 0px 6px 0px 6px;
    }
 

    .question {
        padding-top: 10px;
        text-align: left;
    }
    
    .title {
        color: var(--blue-600);
        text-decoration: none;
        font-size: 15px;
    }

    .question-body {
        padding: 10px 10px 10px 0;
    }

}
.question-author {
    margin-top: 15px;
    flex: 0 0 auto;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.question-author p {
    margin: 0 60px 5px ;
    color: var(--black-400);
}

.author-details{
    display: flex;
}

.author-details img {
    width: 3rem;
    height: 40px;
}

.author {
    display: flex;
    flex-direction: column;
}

.author-name {
    margin: 0px 0 0  8px;
    text-decoration: none;
    color: var(--blue-600);
}

.reputation-score {
    margin: 11px 0 0  8px;
    display: flex;
}

.score {
    margin: 0 8px 0 8px;
}

.score li {
    display: inline-flex;
    margin: 0 3px 0 3px;
}

.score .indicator {
    width: 10px;
    height: 10px;
    border-radius: 50%;
}

ul .gold {
    background: var(--orange-900);    
}

ul .silver {
    background: var(--black-400);    
}

ul .bronze {
    background: var(--yellow-900);    
}

