}
.hot-questions {
    margin: 20px 10px 0 30px;
    text-align: left;
    position: relative;
    z-index: 2;
}

.hot-questions h2 {
    margin: 5px 0 20px 0;
}

.hot-q-flex {
    display: flex;
    justify-content: center;
    margin-bottom: 15px;
    position: relative;
    z-index: 3;
}

.hot-questions .hot-q-ans {
    padding: 10px;
    background: rgb(124, 219, 124);
    color: honeydew;
    border-radius: 10px;
}

.hot-questions .hot-q-title {
    font-size: 15px;
    margin-left: 10px;
}

.leftBarItems {
    margin: 0 auto;
    text-align: left;
    height: 100%;
}

.bar-item {
    padding: 15px;
    cursor: pointer;
}

.bar-item:active {
    background: var(--black-050);
    border-right: 3px solid var(--orange);
}

.bar-item:focus {
    background: var(--black-050);
    border-right: 3px solid var(--orange);
  }

.authentication-page-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    z-index: 1;
    overflow: hidden;
}

.panel-container {
    position: relative;
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}

.panel {
    flex: 1 1 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: -webkit-transform 1.9s ease-in-out;
    transition: transform 1.9s ease-in-out;
    transition: transform 1.9s ease-in-out, -webkit-transform 1.9s ease-in-out;
    transition-delay: 0.6s;
    height: 100%;
    width: 100%;
    position: relative;
    z-index: 3;
}

.panel-image {
    width: 100%;
    transition: -webkit-transform 1.1s ease-in-out;
    transition: transform 1.1s ease-in-out;
    transition: transform 1.1s ease-in-out, -webkit-transform 1.1s ease-in-out;
    transition-delay: 0.4s;
}

.panel div {
    width: 100%;
    margin-top: 40px;
    position: relative;
    z-index: 5;
}

.panel p {
    color: var(--blue-900);
    font-size: 1.2rem;
}

.panel button {
    width: 13rem;
    height: 40px;
    font-weight: 600;
    font-size: 0.9rem;
    border: 2.5px solid var(--blue-900);
    border-radius: 50px;
    background: none;
    margin: 10px;
    cursor: pointer;
}

.auth-forms-container {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}


/* Set container class top Signup mode */
.signup-mode .left-panel {
    -webkit-transform: translateX(-700px);
            transform: translateX(-700px);
}

.signup-mode .signin-form-wrapper {
    opacity: 0;
}

.signup-mode .signup-form-wrapper {
    position: relative;
    z-index: 4;
}


/* Set container class top Signin mode */
.signin-mode .panel-container .right-panel {
    -webkit-transform: translateX(700px);
            transform: translateX(700px);
}

.signin-mode .panel-container .left-panel {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
}

.signin-mode .auth-forms-container .signup-form-wrapper {
    opacity: 0;
}

.signin-mode .auth-forms-container .signin-form-wrapper {
    position: relative;
    opacity: 1;
    z-index: 4;
}
.main{
    display: flex;    
    flex-direction: column;    
    background-color: #fff;    
}
